        <div class="step-content">
          <div class="step-title">去挂号</div>
          <div class="step-desc">点击去挂号<br/>进行实名认证</div>
          <div class="step-image">
            <img src="../assets/guide-appointment.png" alt="去挂号" />
          </div>
        </div>
      </div>
    </div>

    <!-- 底部导航栏 -->
    <div class="bottom-nav">
      <div class="nav-item" @click="goToHomePage">
