<script>
export default {
  name: 'GuideProcess',
  data() {
    return {
      fromReportPage: false
    }
  },
  created() {
    // 获取来源信息
    this.fromReportPage = this.$route.query.fromReportPage === 'true';
  },
  methods: {
    goBack() {
      // 返回首页，并保持原来的状态
      this.$router.push({
        path: '/home',
        query: { 
          showReportPage: this.fromReportPage ? 'true' : 'false'
        }
      });
    },
    goToHomePage() {
      // 返回首页，并保持原来的状态
      this.$router.push({
        path: '/home',
        query: { 
          showReportPage: this.fromReportPage ? 'true' : 'false'
        }
      });
    },
    goToMyPage() {
      // 跳转到我的页面
      this.$router.push({
        path: '/my-page',
        query: { 
          fromReportView: this.fromReportPage ? 'true' : 'false'
        }
      });
    }
  }
}
</script>

